const Content = require('../pages/Content.vue');
const ActivityCenter = require('../pages/activityCenter.vue'); // 
const PublishManage = require('../pages/publishManage.vue'); // 
const ExcellenceShow = require('../pages/excellentWork/list.vue'); // 
const UserCenter = require('../pages/userCenter.vue'); // 
const MyActivity = require('../pages/myActivity.vue'); // 
const MyActivityDetail = require('../pages/myActivityDetail.vue'); // -
const activityProcess = require('../pages/activityProcess/index');
const excellentWork = require('../pages/excellentWork/index');
const excellentWorkDetail = require('../pages/excellentWork/excellentWorkDetail');
const expertReview = require('../pages/expertReview/index');
const expertReviewDetail = require('../pages/expertReview/workDetail');
const expertStore = require('../pages/expertStore');
const auditActivity = require('../pages/auditCenter/auditActivity');
const auditDetail = require('../pages/auditCenter/auditDetail');
const myActivity = require('../pages/myActivity/index');
const messageCenter = require('../pages/messageCenter/index');
const workAuditDetail = require('../pages/myActivity/workAuditDetail');
const Switch = require('../pages/switchPage');
const home=require('../pages/home');
const activityProject=require('../pages/homePages/activityProject');
const activityEnroll=require('../pages/homePages/activityEnroll');
const userEdit=require('../pages/userCenter/userEdit');
const submitEnroll=require('../pages/homePages/submitEnroll');
const activityDetail = require('../pages/activityProcess/activityDetail');


const routes = [
  {
    path: '/',
    components: Content,
    children: [
      { path: '/', components: Switch },
      { path: '/myActivity', components: MyActivity }, // 
      { path: '/myActivityDetail', components: MyActivityDetail }, // 
      { path: '/myActivity/index', components: myActivity }, // 
      { path: '/myActivity/workAuditDetail', components: workAuditDetail }, // 

      { path: '/activityCenter', components: ActivityCenter }, // 
      { path: '/publishManage', components: PublishManage }, // 
      { path: '/excellence', components: ExcellenceShow }, // 
      { path: '/userCenter', components: UserCenter }, // 
      { path: '/activity/process', components: activityProcess }, // 

      { path: '/excellentWork/index', components: excellentWork }, // 
      { path: '/excellentWork/excellentWorkDetail', components: excellentWorkDetail }, // 

      { path: '/expertReview', components: expertReview }, // 
      { path: '/expertReview/workDetail', components: expertReviewDetail }, // 

      { path: '/expertStore', components: expertStore }, // 
      { path: '/auditActivity', components: auditActivity }, // 
      { path: '/auditActivity/auditDetail', components: auditDetail }, // 

      { path: '/msgcenter', components: messageCenter }, // 

      { path: '/home', components: home},//
      { path: '/home/activityProject', components: activityProject},//
      { path: '/home/activityEnroll', components: activityEnroll},//
      { path: '/home/submitEnroll', components: submitEnroll},//

      { path: '/userCenter/userEdit', components: userEdit},//

      { path: '/activityDetail', components: activityDetail}//


      
    ]
  },
  { path: '*', redirect: '/' }
];

export default routes;
